.nav.nav-tabs{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  text-align: center;
  margin-bottom: 50px;
  bottom: 0;
  -ms-flex-align: end;
      align-items: flex-end;
}

.nav-link {
  color: #120f0e;
}

.nav-tabs>li.nav-item>, .nav-tabs>li.nav-item:hover, .nav-tabs>li.nav-item:focus{

  color: #931c00;
  }

  ul.react-reveal.nav.nav-tabs {
    border-bottom: black;
  }
  
  a.nav-link.active {
    color: red;
  }

  @media only screen and (max-width: 375px) {
    .nav.nav-tabs { 
      font-size: 15px;
    }
  }
  
  @media only screen and (max-width: 330px) {
    .nav.nav-tabs { 
      font-size: 12.5px;
    }
  }
  


